<template>
	<view class="express">
		<watermark></watermark>
		<!-- 顶部导航 -->
		<navTabs :list="list" @getChange="_getChange" @getData="_getData" @clickPrj="_clickPrj"></navTabs>
		<view class="tabs_item" v-if="current==0">
			<indexTemplate :followUpData="followUpData" :chartData="chartData" :transactionData="transactionData"></indexTemplate>
		</view>
		<view class="tabs_item" v-if="current==1">
			<indexTemplate :followUpData="followUpData" :chartData="chartData" :transactionData="transactionData"></indexTemplate>
		</view>
		<view class="tabs_item" v-if="current==2">
			<indexTemplate :followUpData="followUpData" :chartData="chartData" :transactionData="transactionData"></indexTemplate>
		</view>
		<tabBar></tabBar>
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import tabBar from '@/components/tabBar.vue';
	import navTabs from '@/components/nav-tabs.vue';
	import indexTemplate from '../components/indexTemplate.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				current:0,
				list: [{
					name: '本日'
				}, {
					name: '本月'
				}, {
					name: '本年'
				}],
				postData:{
					companyId:'', // 公司id
					projectId:'', // 项目id
					timeUnit:'', // 时间维度 2本月 3本日
				},
				followUpData:{}, // 跟进情况
				chartData:{
					series:[]
				}, 
				transactionData:[], // 成交情况
			}
		},
		methods: {
			// 获取首页数据
			_getList(){
				let that = this
				that.postData.timeUnit = that.current == 0 ? 3 : (that.current == 2 ? 0 : 2)
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3005', Object.assign({}, that.postData), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.followUpData = res.data.data.followUpData
						that.chartData = res.data.data.reFundsData
						// that.reFundsData.series = res.data.data.reFundsData.series
						that.transactionData = res.data.data.transactionData
						bus.$emit('changePie', that.chartData)
					}
				})
			},
			// 点击公司回调
			_getData(val, val2){
				this.postData.companyId = val
				this.postData.projectId = val2
				this._getList()
			},
			// 点击项目回调
			_clickPrj(val){
				this.postData.projectId = val
				this._getList()
			},
			// tabs切换
			_getChange(index){
				this.current = index
				this._getList()
			}
		},
		components: {
			watermark, tabBar, navTabs, indexTemplate
		},
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
</style>
